export default "import React from 'react';\nimport {\n    ComponentsRepo,\n    AutoFields,\n    AutoItems,\n    clickEventHandler\n} from '@autoviews/core';\nimport {Box, List, ListItem, ListItemText, Chip, Divider} from '@mui/material';\n\nexport const repo = new ComponentsRepo('MyListRepo')\n    .register('array', {\n        name: 'MyList',\n        component: props => (\n            <Box\n                component=\"form\"\n                sx={{'& .MuiTextField-root': {m: 1, width: '25ch'}}}\n                noValidate\n                autoComplete=\"off\"\n            >\n                <List\n                    sx={{bgcolor: 'background.paper'}}\n                    dense\n                >\n                    <AutoItems {...props} />\n                </List>\n            </Box>\n        )\n    })\n    .register('object', {\n        name: 'MyObject',\n        component: props => (\n            <>\n                <ListItem>\n                    <AutoFields\n                        pick={['login', 'active']}\n                        {...props}\n                    />\n                </ListItem>\n                <Divider component=\"li\" />\n            </>\n        )\n    })\n    .register('string', {\n        name: 'MyStringComponent',\n        component: props => <ListItemText>{props.data}</ListItemText>\n    })\n    .register('boolean', {\n        name: 'MyBooleanComponent',\n        component: props => (\n            <Chip\n                label={props.data ? 'online' : 'offline'}\n                color={props.data ? 'success' : 'error'}\n                onClick={clickEventHandler({\n                    onClick: props.onClick,\n                    pointer: props.pointer,\n                    schemaPointer: props.schemaPointer,\n                    data: {\n                        type: 'CHANGE_ACTIVE_STATE',\n                        value: !props.data\n                    }\n                })}\n            />\n        )\n    });\n";